import styled from 'styled-components'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faQuestionCircle } from '@fortawesome/free-solid-svg-icons'
import { ADMIN_COLOR, FONT_SIZE } from '../../../constants/style'

const Wrapper = styled.div`
  padding: 10px 40px;
  div {
    margin: 8px 0px;
  }
`

const Archived = styled.span`
  margin-left: 4px;
  color: ${ADMIN_COLOR.Btn_grey};
  span {
    opacity: 0;
    transition: opacity 0.2s;
    padding: 4px;
    border-bottom: 1px dotted black;
    font-size: ${FONT_SIZE.xs};
    position: relative;
    bottom: 8px;
    left: 8px;
  }
  &:hover {
    span {
      opacity: 1;
    }
  }
`

export default function OrderInfo({ orderDetail }) {
  return (
    <Wrapper>
      <div>
        {orderDetail.status}
        {orderDetail.isDeleted ? (
          <Archived>
            <FontAwesomeIcon icon={faQuestionCircle} />
            <span></span>
          </Archived>
        ) : (
          <></>
        )}
      </div>
      <div>{orderDetail.ticketNo}</div>
      <div>{orderDetail.orderName}</div>
      <div>{orderDetail.orderAddress}</div>
      <div>{orderDetail.orderEmail}</div>
      <div>{orderDetail.orderPhone}</div>
      <div>{orderDetail.payment}</div>
      <div>{orderDetail.shipping}</div>
    </Wrapper>
  )
}
