import styled from 'styled-components'
import { GeneralBtn } from '../../../components/Button'
import { FullModal } from '../../Modal'

const Wrapper = styled.div`
  position: absolute;
  top: 22%;
  right: 40px;
  display: flex;
  button {
    padding: 0 20px;
    margin: 0 8px;
    transition: 0.2s ease;
  }
`

export default function Buttons({
  orderDetail,
  handleOrderStatus,
  handleArchive,
  isModalOpen,
  setIsModalOpen
}) {
  return (
    <Wrapper>
      {orderDetail.status === '' && (
        <>
          <GeneralBtn
            onClick={() => handleOrderStatus('ship')}
            color='admin_blue'
            children=''
          />
          <GeneralBtn
            onClick={() => handleOrderStatus('cancel')}
            color='admin_grey'
            children=''
          />
        </>
      )}
      {orderDetail.status === '' && (
        <GeneralBtn
          onClick={() => handleOrderStatus('complete')}
          color='admin_grey'
          children=''
        />
      )}
      {orderDetail.status === '' && !orderDetail.isDeleted && (
        <GeneralBtn
          onClick={() => handleArchive(orderDetail.ticketNo)}
          color='admin_grey'
          children=''
        />
      )}
      {orderDetail.status === '' && !orderDetail.isDeleted && (
        <GeneralBtn
          onClick={() => handleArchive(orderDetail.ticketNo)}
          color='admin_grey'
          children=''
        />
      )}
      <FullModal
        open={isModalOpen}
        content=''
        buttonOne={
          <GeneralBtn
            onClick={() => handleOrderStatus('cancel')}
            color='admin_blue'
            children=''
          />
        }
        buttonTwo={
          <GeneralBtn
            onClick={() => setIsModalOpen(false)}
            color='admin_grey'
            children=''
          />
        }
        onClose={() => setIsModalOpen(false)}
        desk='back'
      />
    </Wrapper>
  )
}
